<template>
  <div class="scan">
    <div class="contain">
      <img src="../assets/scan.png" alt>
      <template v-if="status==0">
        <p class="title">{{message}}</p>
        <p class="msg">:{{orderNumber.c_qrcode}}</p>
        <p class="msg">:{{orderNumber.c_weixin}}</p>
      </template>
      <template v-if="status==4" class="title">
        <p class="title">{{message}}</p>
      </template>
      <p class="msg"></p>
    </div>
    <div class="scanBtn" @click="scan">
      <img src="../assets/scanicon.png" alt>
      <span></span>
    </div>
  </div>
</template>
<script>
import wx from "weixin-js-sdk";
import scanAPI from "@/pages/index/services/scan";
export default {
  data() {
    return {
      scanResultCode: "",
      orderNumber: "",
      show: false,
      status:'',
      message:''
    };
  },
  methods: {
    scan: function() {
      const params = {
        url: window.location.href,
        company_id: window.localStorage.getItem("company_id")
      };
      const _this = this;
      scanAPI.wxSdkConfig({ data: params }).then(res => {
        const { data } = res.data;
        wx.config({
          beta: true, // wx.invokejsapi
          // ,apialertpclogpc
          // debug: true,
          // 
          appId: res.data.appid,
          // 
          timestamp: data.timestamp,
          // 
          nonceStr: data.nonceStr,
          // 
          signature: data.signature,
          // JSJS
          jsApiList: ["scanQRCode"]
        });
      });

      wx.ready(function() {
        wx.scanQRCode({
          desc: "scanQRCode desc",
          needResult: 1, // 01
          scanType: ["qrCode"], // 
          success: function(res) {
            var result = res.resultStr; // needResult  1 
            if (result == _this.scanResultCode) {
              // _this.show = false;
              _this.$toast("");
            } else {
              _this.scanResultCode = result;
            }
            // if (result.slice(0, 6) == "hgqc||") {
            //   if (result.slice(6) == _this.scanResultCode) {
            //     _this.show = false;
            //   } else {
            //     _this.scanResultCode = result.slice(6);
            //   }
            // } else {
            //   _this.$toast(`:${result}`);
            // }
          }
        });
      });
    },
    getOrderNumber() {
      const data = {
        c_qrcode: this.scanResultCode,
        company_id: localStorage.getItem("company_id")
      };
      scanAPI.fullcode({ data: data }).then(res => {
        const { status, message, data } = res.data;
        this.status = status;
        this.message = message;
        if (status == 3) {
          this.$toast(message);
        }else if (status == 0) {
          this.orderNumber = data;
        }
      });
    }
  },
  created() {
    this.scanResultCode = this.$route.query.code;
  },
  watch: {
    //  `question` 
    scanResultCode: function() {
      this.getOrderNumber();
    }
  }
};
</script>
<style lang="scss" scoped>
.scan {
  .contain {
    display: flex;
    display: -webkit-flex; /* Safari */
    flex-direction: column;
    justify-content: center;
    align-items: center;
    img {
      width: 380px;
      height: 308px;
      margin: 48px 0;
    }
    .title {
      font-size: 32px;
      color: #0a9bca;
    }
    .msg {
      font-size: 28px;
      color: #727171;
      padding: 10px 50px;
    }
  }
  .scanBtn {
    width: 340px;
    height: 88px;
    margin: 30px auto;
    background: #51b8cb;
    border-radius: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      width: 52px;
      height: 52px;
    }
    span {
      text-indent: 16px;
      font-size: 34px;
      color: rgba(255, 255, 255, 1);
    }
  }
}
</style>



